<template>
  <div id="particulars">
    <van-nav-bar>
      <div slot="title">
        详情页面
      </div>
      <van-icon name="arrow-left" slot="left" color="black" @click="$router.go(-1)"/>
    </van-nav-bar>
    <div>
      <h3>{{titles}}</h3>
    </div>
    <div v-html="contents" style="text-align:justify!important;padding: 0 0.8rem;text-indent:2em;">
      {{contents}}
    </div>
  </div>
</template>

<script>
  export default {
    name: "particulars",
    data() {
      return {

      }
    },
    created() {
      this.contents = this.$route.query.contents//获取url里的详情内容
      this.titles = this.$route.query.titles//获取url里的标题
    },
    methods: {

    },
  }
</script>

<style scoped>
  #container img {
    width: 100%;
    height: 100%;
  }

</style>



